import React from "react";
import PropTypes from "prop-types";

import { ButtonGroup, Button, Tooltip } from "@salesforce/design-system-react";

const propTypes = {
  totalItems: PropTypes.number.isRequired,
  onChangePage: PropTypes.func.isRequired,
  currentPage: PropTypes.number,
  pageSize: PropTypes.number
};

const defaultProps = {
  currentPage: 1,
  pageSize: 10
};

const getPager = (totalItems, currentPage, pageSize) => {
  // default to first page
  // eslint-disable-next-line no-param-reassign
  currentPage = currentPage || 1;

  // default page size is 10
  // eslint-disable-next-line no-param-reassign
  pageSize = pageSize || 10;

  let isNarrow = false;

  if (totalItems > 10000) {
    // eslint-disable-next-line no-param-reassign
    totalItems = 10000;
    isNarrow = true;
  }

  // calculate total pages
  const totalPages = Math.ceil(totalItems / pageSize);

  let startPage;
  let endPage;
  if (totalPages <= 10) {
    // less than 10 total pages so show all
    startPage = 1;
    endPage = totalPages;
  } else {
    // more than 10 total pages so calculate start and end pages
    // eslint-disable-next-line no-lonely-if
    if (currentPage <= 6) {
      startPage = 1;
      endPage = 10;
    } else if (isNarrow && currentPage + 3 >= totalPages) {
      startPage = totalPages - 8;
      endPage = totalPages;
    } else if (currentPage + 4 >= totalPages) {
      startPage = totalPages - 9;
      endPage = totalPages;
    } else {
      startPage = currentPage - 5;
      endPage = currentPage + 4;
    }
  }

  // calculate start and end item indexes
  const startIndex = (currentPage - 1) * pageSize;
  const endIndex = Math.min(startIndex + pageSize - 1, totalItems - 1);

  // create an array of pages to ng-repeat in the pager control
  const pages = [...Array(endPage + 1 - startPage).keys()].map(i => startPage + i);

  // return object with all pager properties required by the view
  return {
    totalItems,
    currentPage,
    pageSize,
    totalPages,
    startPage,
    endPage,
    startIndex,
    endIndex,
    pages,
    isNarrow
  };
};

class SLDSPaginator extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      pager: {}
    };
  }

  // eslint-disable-next-line react/no-deprecated
  componentWillMount() {
    // set page if items array isn't empty
    const { totalItems, currentPage } = this.props;
    if (totalItems) {
      this.setPage(currentPage, true);
    }
  }

  static getDerivedStateFromProps(nextProps, prevState) {
    const {
      totalItems: newTotalItems,
      pageSize: newPageSize,
      currentPage: newCurrentPage
    } = nextProps;
    const { totalItems, pageSize, page } = prevState.pager;
    if (totalItems !== newTotalItems || pageSize !== newPageSize || newCurrentPage !== page) {
      return { pager: getPager(newTotalItems, newCurrentPage, newPageSize) };
    }
    return null;
  }

  setPage(page, isInitialLoad) {
    const { totalItems, pageSize } = this.props;
    let { pager } = this.state;

    if (page < 1 || page > pager.totalPages) {
      return;
    }

    // get new pager object for specified page
    pager = getPager(totalItems, page, pageSize);

    // update state
    this.setState({ pager });

    // call change page function in parent component
    // eslint-disable-next-line react/destructuring-assignment
    this.props.onChangePage(page, false || isInitialLoad);
  }

  render() {
    const { pager } = this.state;

    if (!pager.pages || pager.pages.length <= 1) {
      // don't display pager if there is only 1 page
      return null;
    }

    return (
      <ButtonGroup>
        <Button
          iconCategory="utility"
          iconName="left"
          iconPosition="left"
          label="First"
          variant="neutral"
          onClick={() => this.setPage(1)}
          disabled={pager.currentPage === 1}
        />
        <Button
          iconCategory="utility"
          iconName="chevronleft"
          iconPosition="left"
          variant="neutral"
          onClick={() => this.setPage(pager.currentPage - 1)}
          disabled={pager.currentPage === 1}
        />
        {pager.pages.map((page, index) => {
          return (
            <Button
              // eslint-disable-next-line react/no-array-index-key
              key={`SLDSPaginator${Date.now()}${index}`}
              label={page}
              variant={pager.currentPage === page ? "brand" : "neutral"}
              onClick={() => this.setPage(page)}
            />
          );
        })}
        {pager.isNarrow && pager.currentPage >= pager.totalPages - 3 && (
          <Tooltip
            align="top right"
            content="The list is too long, try adding more filters to optimize the results."
          >
            <Button label="&middot;&middot;&middot;" variant="neutral" />
          </Tooltip>
        )}
        <Button
          iconCategory="utility"
          iconName="chevronright"
          iconPosition="right"
          variant="neutral"
          onClick={() => this.setPage(pager.currentPage + 1)}
          disabled={pager.currentPage === pager.totalPages}
        />
        <Button
          iconCategory="utility"
          iconName="right"
          iconPosition="right"
          label="Last"
          variant="neutral"
          onClick={() => this.setPage(pager.totalPages)}
          disabled={pager.currentPage === pager.totalPages}
        />
      </ButtonGroup>
    );
  }
}

SLDSPaginator.propTypes = propTypes;
SLDSPaginator.defaultProps = defaultProps;
export default SLDSPaginator;
